.lined-btn { display: inline-block; width: 200px; padding: 15px 24px; border-radius: 4px; border: 1px solid #d6d6d6; font-size: 16px; font-weight: bold; line-height: normal; text-align: center; color: #363636; background-color: #fff; vertical-align: middle; font-family: SamsungOne;} 
.filled-btn:hover:not([disabled]) { background-color: #002ba3; } 
.lined-btn:hover:not([disabled]) { background-color: #d6d6d6; } 
/* /button */
/* checkbox */
.checkbox-wrap.error + .error-msg { display: block; }
.checkbox-wrap + .error-msg { font-size: 14px; font-weight: normal; font-stretch: normal; font-style: normal; line-height: 1.43; letter-spacing: normal; color: #ec2000; display: none; margin: 8px 0 0 0; }
.check-wrap { display: block; padding: 9px 40px; background: #fff; position: relative; } 
.check-wrap input[type="checkbox"] { position: absolute; opacity: 0; cursor: pointer; height: 0; width: 0; } 
.check-wrap input[type="checkbox"] + label span { position: relative; display: inline-block; padding: 2px 12px 0 26px; font-size: 16px; line-height: 1.25; letter-spacing: -0.11px; color: #363636; } 
.check-wrap input[type="checkbox"] + label span:before { content: ""; position: absolute; left: 0; top: 50%; transform: translateY(-50%); width: 16px; height: 16px; background: url(/images/icon_checkbox.png) no-repeat center / cover; } 
.check-wrap.required input[type="checkbox"] + label span:after { content: "*"; position: absolute; right: 0; top: 0; font-size: 16px; line-height: 1.25; letter-spacing: -0.11px; color: #ec2000; } 
.check-wrap input[type="checkbox"]:checked + label span:before { background-image: url(/images/icon_checkbox_on.png) !important; } 
.check-wrap.required input[type="checkbox"]:checked + label span:before { opacity: 0.4; } 
.check-wrap.required input[type="checkbox"]:checked + label { pointer-events: none; } 
.check-wrap.error input[type="checkbox"] + label span:before { background-image: url(/images/icon_checkbox_error.png); }

.check-wrap .handle { position: absolute; right: 40px; top: 50%; transform: translateY(-50%); width: 24px; height: 24px; display: none; }
.check-wrap .handle img {width: 100%;}
.ui-sortable-helper .check-wrap { box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.2); }
@media (hover: hover) {
  .check-wrap:hover .handle { display: block; }
}
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .check-wrap:hover .handle { display: block; }
}
/* /checkbox */
/* radio */
.excel-radio + .excel-radio {margin-top:10px;}
.excel-radio input { position: absolute; opacity: 0; cursor: pointer; height: 0; width: 0; } 
.excel-radio label .text { position: relative; display: inline-block; padding-left: 24px; font-size: 16px; line-height: 1.5; letter-spacing: -0.16px; color: #363636; cursor: pointer; text-align: left; } 
.excel-radio label .text:before { content: ""; position: absolute; left: 0; top: 2px; width: 16px; height: 16px; background: url(/images/radio-off.png) no-repeat center / cover; } 
.excel-radio input:checked + label .text:before { background-image: url(/images/radio-on.png); } 
/* /radio */

.right-wrap { margin-bottom: 40px; text-align: right; } 
.right-wrap .check-wrap + .filled-btn { margin-left: 16px; } 
/* input */
.input-text { position: relative; padding: 0 12px; border-radius: 4px; border: solid 1px #d6d6d6; background-color: #fff; } 
.input-text.big { padding: 0 12px; } 
.input-text.search { padding-left: 32px; background: url(/images/input-search.png) no-repeat #fff 8px center / 16px 17px; } 
.input-text input[type="text"] { width: 100%; height: 30px; line-height: 20px; font-family: SamsungOne,"Apple SD Gothic Neo","Apple SD 고딕 Neo",AppleGothic,arial,sans-serif; font-size: 14px; color: #363636; } 
.input-text input[type="text"] + button { display: none; position: absolute; right: 8px; top: 50%; transform: translateY(-50%); width: 16px; height: 16px; padding: 3px; line-height: 0; font-size: 0; } 
.input-text input[type="text"] + button img { max-width: 100%; } 
.input-text.active input[type="text"] + button { display: block; } 
.input-text.active.error input[type="text"] + button { display: none; } 
.input-text.active { padding-right: 32px; } 
.input-text.big input[type="text"] { height: 46px; font-size: 16px; line-height: 1.38; } 
.input-text.active { border-color: #0f42cf; } 
.input-text.error { border-color: #ec2000; padding-right: 36px; background: url(/images/error.png) no-repeat #fff calc(100% - 9px) center / 16px; } 
.input-text.error + .error-msg { display: block; } 
.input-text.error { border-color: #ec2000; padding-right: 36px; background: url(/images/error.png) no-repeat #fff calc(100% - 9px) center / 16px; } 
.input-text.big.error { padding-right: 48px; background-size: 24px; } 
.input-file { position: relative; border-radius: 4px; border: solid 1px #d6d6d6; } 
.input-file input[type="file"] { position: absolute; opacity: 0; cursor: pointer; height: 0; width: 0; } 
.input-file input[type="file"] + label { display: block; width: 32px; height: 32px; font-size: 0; line-height: 0; background: url(/images/attachment.png) no-repeat center transparent; background-size: 16px; cursor: pointer; } 
.textarea { width: 100%; height: 100px; font-size: 14px; line-height: 1.43; letter-spacing: -0.14px; color: #363636; resize: none; scrollbar-width: none; -ms-overflow-style: none; overflow-x: scroll; } 
.textarea::-webkit-scrollbar { display: none; } 
/* /input */
.comment { position: relative; margin-top: 16px; padding: 16px 16px 36px; border-radius: 4px; border: solid 1px #d6d6d6; background-color: #fff; } 
.comment .textarea { height: 228px; font-size: 16px; line-height: 1.38; color: #000; font-family: SamsungOne; }
.comment.small .textarea { height: 108px; }
.comment.error { border-color: #ec2000; } 
.error-msg { display: none; margin: 8px 0 0 0; font-size: 14px; line-height: 1.43; letter-spacing: normal; color: #ec2000; } 
.comment.error + .error-msg { display: block; } 
.comment.active .count .cur { color: #363636; }
.comment .count { position: absolute; bottom: 8px; right: 12px; font-size: 14px; line-height: 1.43; letter-spacing: -0.14px; color: #9e9e9e; }

@media all and (max-width: 1000px){
 .comment { margin-top: 8px; } 
 .comment .textarea {font-size: 14px;}
.input-file input[type="file"] + label { width: 40px; background-size: 24px; } 
.input-text.big { padding: 0 12px; } 
.input-text.big input[type="text"] { height: 38px; font-size: 14px; }
 }

 .attach-wrap { position: relative; display: flex; flex-direction: column; justify-content: center; min-height: 160px; padding: 16px; border: 2px solid rgba(214,214,214,0.5); text-align: center; border-radius: 4px; } 
.attach-wrap.attached { display: block; text-align: left; } 
.attach-wrap.attaching { border: 1px solid #0f42cf; } 
.attach-wrap.error { border: 1px solid #ec2000; } 
.attach-wrap.error + .error-msg { display: block; } 
.attach-wrap .text { display: block; margin-bottom: 16px; font-size: 16px; line-height: 1.5; color: #363636; } 
.attach-wrap .file-btn input[type="file"] { position: absolute; width: 0; height: 0; opacity: 0; cursor: pointer; } 
.attach-wrap .file-btn input[type="file"] + label { display: inline-block; padding: 7px 24px; border-radius: 4px; font-size: 14px; font-weight: bold; line-height: normal; color: #363636; cursor: pointer; border: 1px solid rgba(0, 0, 0, 0.2) } 

.attach-wrap .attached-item { position: relative; padding: 17px 24px; margin-top: 0 !important; border: solid 1px #d6d6d6; background-color: #f7f7f7; } 
.attach-wrap .attached-item .name { display: block; max-width: 100%; margin-right: 28px; padding: 0 0 0 28px; font-size: 14px; line-height: 1.43; text-overflow: ellipsis; overflow: hidden; max-height: 40px; -webkit-box-orient: vertical; -webkit-line-clamp: 2; display: -webkit-box; color: #0f42cf; background: url(/images/file.png) no-repeat left 2px; background-size: 16px; font-family: Arial; } 
.attach-wrap .attached-item a:hover {text-decoration: underline;}
.attach-wrap .attached-item button { position: absolute; top: 12px; right: 16px; width: 16px; height: 16px; line-height: 0; font-size: 0; } 
.attach-wrap .attached-item button img { max-width: 100%; } 

.test-bottom { margin-top: 40px; text-align: center; } 
.test-bottom .notice { display: block; margin-bottom: 12px; font-size: 16px; line-height: 1.5; color: #9e9e9e; } 

/* filter-wrap */
.filter-wrap { margin-top: 40px; } 
.filter-box ul { display: flex; margin: 0 -4px; } 
.filter-box ul li { position: relative; flex: 1 1 0px; width: 16.66%; padding: 0 4px; } 
.filter-item { position: relative; text-align: left; } 
.filter-item .title { display: block; margin-bottom: 6px; font-size: 14px; font-weight: bold; line-height: 1.43; letter-spacing: -0.14px; color: #6e6e6e; } 
.filter-item .input-text.active + .dropdown-wrap { display: block; } 
.filter-item .selected-text { position: relative; display: block; padding: 13px 36px 13px 16px; border-radius: 4px; border: solid 1px #d6d6d6; font-size: 16px; line-height: 1.38; color: #363636; } 
.filter-item .selected-text:before { content: ""; position: absolute; top: 50%; right: 12px; transform: translateY(-50%); width: 16px; height: 16px; background: url(/images/arrow-light.png) no-repeat center / cover; } 
.filter-item .selected-text.active { border-color: #0f42cf; } 
.filter-item .selected-text.active:before { transform: translateY(-50%) rotate(180deg); } 
.filter-item .selected-text span { display: block; max-width: 100%; text-overflow: ellipsis; overflow: hidden; white-space: nowrap; } 
.filter-item .selected-text.active + .dropdown-wrap { display: block; } 
.filter-item.error .selected-text { border-color: #ec2000; } 
.filter-item.error + .error-msg { display: block; } 
.dropdown-wrap { display: none; position: absolute; top: calc(100% + 4px); left: 0; right: 0; background-color: #fff; z-index: 10; padding-bottom: 40px; margin-top: 0 !important; } 
.dropdown-wrap ul { display: block; margin: 0; padding: 8px 0; border-radius: 4px; border: solid 1px #d6d6d6; } 
.dropdown-wrap ul li { flex: none; width: 100%; padding: 0; } 
.dropdown-wrap ul li + li { margin-top: 0 !important; } 
.dropdown-wrap ul li a { display: block; padding: 9px 16px; font-size: 16px; line-height: 1.38; } 
.dropdown-wrap ul li.recent a { padding-left: 40px; background: url(/images/icon_time.png) 16px center no-repeat; background-size: 16px; } 
.dropdown-wrap ul li:hover a { background-color: rgba(15,66,207,0.1); } 
.filter-selected { margin-top: 6px; padding: 24px; border: solid 1px #d6d6d6; border-radius:4px; } 
.filter-selected .textarea { height: 184px; font-size: 16px; line-height: 1.5; letter-spacing: normal; color: #363636; } 
.bottom { margin-top: 40px; text-align: center; } 
.reg-item + .bottom { margin-top: 60px; } 
.bottom ul { text-align: center; } 
.bottom ul li { display: inline-block; padding: 0 8px; } 
@media all and (max-width: 1000px){
 .attach-wrap.only { height: 330px; } 
 .filter-wrap { margin-top: 24px; } 
 .filter-box ul { display: block; margin: 0; } 
 .filter-box ul li { flex: none; width: 100%; padding: 0; } 
 .filter-box > ul > li + li { margin-top: 16px; } 
 .filter-selected { margin-top: 8px; } 
.filter-title {margin-bottom:0;}
 .bottom .filled-btn.big { width: 100%; } 
 .bottom .lined-btn.big { width: 100%; } 
 .bottom ul { text-align: center; margin: 0 -4px; } 
 .bottom ul li { display: inline-block; width: 50%; padding: 0 4px; } 
 .reg-item + .bottom { margin-top: 40px; } 
.input-text input[type="text"] + button { right: 12px; }
 .filter-item .selected-text { padding-top: 8px; padding-bottom: 8px; font-size: 14px; } 
 .dropdown-wrap ul li a {font-size: 14px;}
 }

 .attach-wrap + .input-text-wrap { margin-top: 40px; } 
 .input-text-wrap { position: relative; } 
.input-text-wrap .text { position: relative; display: inline-block; margin-right: 4px; padding-right: 8px; vertical-align: middle; font-size: 14px; font-weight: bold; line-height: 1.43; letter-spacing: -0.14px; color: #9e9e9e; } 
.input-text-wrap .text:before { content: ":"; position: absolute; top: 50%; right: 0; transform: translateY(-50%); } 
.input-text-wrap .picker-wrap { display: inline-block; width: 260px; vertical-align: middle; } 
.input-text-wrap .select-wrap { display: inline-block; width: 260px; vertical-align: middle; } 
.input-text-wrap .selects > button { width: 100%; height: 48px; font-size: 16px; line-height: 1.38; color: #9e9e9e; } 
.input-text-wrap .selects > button.active + .absList { display: block; } 
.input-text-wrap .selects .absList { z-index: 10; } 

/* datetimepicker */
.mx-datepicker-range {width: 100%; } 
.mx-datepicker >>> .mx-input {max-width: 100%; height: 48px;padding: 13px 70px 13px 16px; border-color: #d6d6d6;white-space: nowrap;text-overflow: ellipsis;overflow: hidden;font-size: 16px; line-height: 1.38; letter-spacing: normal; color: #000; font-family: SamsungOne; } 
.mx-datepicker >>> .mx-input:hover {border-color: #d6d6d6; } 
.mx-datepicker >>> .mx-input:focus {border-color: #0f42cf; } 
.mx-datepicker >>> .mx-input::placeholder {font-weight: normal !important; } 
.mx-datepicker >>> .mx-icon-calendar {right: 12px; } 
.picker-wrap >>> .mx-icon-clear {right: 44px; font-size: 0; }
.clear-icon {display: block;width: 16px;height: 16px; }  
.mx-datepicker >>> .calendar-icon {display: block;width: 24px;height: 24px; } 
.mx-datepicker >>> .calendar-icon img, .clear-icon img { width: 100%; } 
.mx-datepicker.active .calendar-icon { opacity: 1; } 
.mx-icon-left:before, .mx-icon-right:before, .mx-icon-double-left:before, .mx-icon-double-right:before, .mx-icon-double-left:after, .mx-icon-double-right:after {width: 14px;height: 14px;margin-left: -4px;border-width: 3px 0 0 3px;border-color: #000; } 
.mx-calendar-panel-month .mx-icon-left:after, .mx-calendar-panel-month .mx-icon-right:after, .mx-calendar-panel-month .mx-icon-double-left:after, .mx-calendar-panel-month .mx-icon-double-right:after {display: none; } 
.mx-calendar-panel-year .mx-icon-left:after, .mx-calendar-panel-year .mx-icon-right:after, .mx-calendar-panel-year .mx-icon-double-left:after, .mx-calendar-panel-year .mx-icon-double-right:after {display: none; } 
.picker-wrap >>> .mx-input-wrapper .mx-icon-clear{display:block}
.picker-wrap >>> .mx-input-wrapper:hover .mx-icon-clear{display:block}
.picker-wrap >>> .mx-input-wrapper:hover .mx-icon-clear+.mx-icon-calendar{display:block}


/* calendar */
.mx-datepicker-main {border-radius: 4px; } 
.mx-datepicker-header {padding: 12px 0;border-color: #ebebed; } 
.mx-datepicker-header .mx-btn { margin: 0 7px;padding: 2px 8px; border-radius: 4px;font-size: 12px; line-height: 1.67; letter-spacing: -0.12px; color: rgba(54,54,54,0.8);font-family: SamsungOne,"Apple SD Gothic Neo","Apple SD 고딕 Neo",AppleGothic,arial,sans-serif; } 
.mx-datepicker-header .mx-btn:first-child {margin-left: 5px; } 
.mx-datepicker-header .mx-btn:hover { background-color: rgba(15,66,207,0.1); } 
.mx-datepicker-footer {display: none;padding: 12px 16px; } 
.mx-datepicker-footer.on {display: block; } 
.mx-datepicker-footer .mx-btn {padding: 5px 16px 4px; border-radius: 4px; border: solid 1px #0f42cf;color: #0f42cf;font-weight: bold;font-size: 12px;font-family: SamsungOne,"Apple SD Gothic Neo","Apple SD 고딕 Neo",AppleGothic,arial,sans-serif;line-height: normal; } 
.mx-calendar {padding: 0 16px 32px; } 

/* header, footer */
.mx-calendar-header, .mx-time-header {margin-top: 16px;height: 36px;line-height: 36px; } 
.mx-calendar-header-label .mx-btn,
.mx-time-header .mx-btn,
.mx-calendar-header-label {font-size: 16px;font-family: SamsungOne,"Apple SD Gothic Neo","Apple SD 고딕 Neo",AppleGothic,arial,sans-serif;font-weight: bold;color: #363636; } 
.mx-table th {font-size: 12px;font-weight: normal; color: #d6d6d6; } 
.mx-table-date td {color: #363636;font-family: SamsungOne,"Apple SD Gothic Neo","Apple SD 고딕 Neo",AppleGothic,arial,sans-serif; } 
.mx-table-date .cell.not-current-month {color: #d6d6d6; } 
.mx-table-date .today {color: #0f42cf; } 
.mx-calendar-content .cell.active {border-radius: 4px;background-color: #0f42cf; } 
.mx-calendar-content .cell.in-range,
.mx-calendar-content .cell.hover-in-range,
.mx-calendar-content .cell:not(.active):hover {background-color: rgba(15,66,207,0.1); } 

/* time */
.mx-time-content {padding: 0 26px; } 
.mx-time-header {border: none; } 
.mx-time-column {margin: 0 5px;border: none; } 
.mx-time-column .mx-time-item {height: 24px;line-height: 24px;border-radius: 4px;color: #363636;font-family: SamsungOne,"Apple SD Gothic Neo","Apple SD 고딕 Neo",AppleGothic,arial,sans-serif; } 
.mx-time-column .mx-time-item:hover {background-color: rgba(15,66,207,0.1); } 
.mx-time-column .mx-time-item.active {color: #fff;font-weight: normal;background-color: #0f42cf; } 
.mx-time-column .mx-time-list::after {display: none; } 

/* month, year */
.mx-calendar-panel-month,
.mx-calendar-panel-year {padding: 0 36px; } 
.mx-calendar-panel-month .mx-calendar-header, .mx-time-header,
.mx-calendar-panel-year .mx-calendar-header, .mx-time-header {margin: 16px -20px 0; } 
.mx-table.mx-table-month {height: 180px; } 
.mx-table-month .cell,
.mx-table-year .cell {padding: 0 4px;color: #363636;font-family: SamsungOne,"Apple SD Gothic Neo","Apple SD 고딕 Neo",AppleGothic,arial,sans-serif; } 
.mx-table-month .cell div,
.mx-table-year .cell div {padding: 5px;border-radius: 4px; } 
.mx-table-month .cell.active,
.mx-table-year .cell.active {background-color: transparent; } 
.mx-table-month .cell.active div,
.mx-table-year .cell.active div {padding: 5px 0;border-radius: 4px;background-color: #0f42cf; } 
.mx-table-month .cell:not(.active):hover,
.mx-table-year .cell:not(.active):hover {background-color: transparent;color: #363636; } 
.mx-table-month .cell:not(.active):hover div,
.mx-table-year .cell:not(.active):hover div {background-color: rgba(15,66,207,0.1); } 
/* /datetimepicker */
@media all and (max-width:1000px){
 .input-text-wrap .text { display: block; margin-right: 0; margin-bottom: 8px; padding-right: 0; } 
 .input-text-wrap .text:before { display: none; } 
 .input-text-wrap .picker-wrap { width: 100%; } 
 .mx-calendar,
 .mx-time { width: 100%; } 
