 }


.pop-wrap {position: fixed; left:0; top:0; bottom: 0; width:100%; display:none; z-index:31;}
.pop-wrap::before {content:''; position: absolute; left:0; top:0; width:100%; height:100%; background:#000; opacity: 0.3;}
.pop-wrap .tit_box {position: relative; padding:0 40px;}
.pop-wrap .tit_box .close {position: absolute; right:40px; top:50%; transform: translateY(-50%);}
.pop-wrap .tit_box .close img {width:24px;}
.pop-wrap .tit_box h3 {font-size: 24px; font-weight: bold; line-height: 1.33; letter-spacing: -0.24px; color: #000000;}
.pop-wrap .scrollHeight {margin-top:40px; height:676px; max-height:calc(100vh - 330px); overflow-y:auto;  padding:40px 40px 0; border-top: 1px solid #ebebed;  border-bottom: 1px solid #ebebed; }
.pop-wrap .scrollHeight:after { content: ""; display: block; padding-bottom: 40px; }
