.pop-wrap .scrollHeight li strong.star {position: relative; padding-right:10px;}
.pop-wrap .scrollHeight li strong.star::after {content:'*'; font-size: 14px; font-weight: bold; line-height: 1.14; letter-spacing: -0.1px; color: #ec2000; position: absolute; right:0; top:-2px;}
.pop-wrap .scrollHeight li div {margin-top:8px;}
.pop-wrap .scrollHeight li select {width:100%; height: 48px; border-radius: 4px; border: solid 1px #d6d6d6; background-color: #ffffff; box-sizing: border-box; appearance:none; background:url(/images/icon_select_black.png) 98% center no-repeat; background-size:16px; font-size: 16px; line-height: 1.38; color: #363636; padding:0 16px;}
.pop-wrap .scrollHeight li + li {margin-top:16px;}
.pop-wrap .bnt_botten { display:flex; justify-content: center;  padding:40px 40px 0;}
.pop-wrap .bnt_botten button { width: 96px; margin: 0 8px; height:48px; font-size: 16px; font-weight: bold; line-height: 1.5; text-align: center; color: #363636; border-radius: 4px; border: solid 1px #d6d6d6; font-family: SamsungOne;}
.pop-wrap .bnt_botten button.ok {border-color:#0f42cf; background:#0f42cf; color:#fff;}
.pop-wrap .bnt_botten button.ok.ready {opacity: 0.3;}
