.pop-wrap .edit-pop {position: absolute; left:50%; top:50%; transform: translate(-50%, -50%); padding:40px 0; width:560px; max-height: 920px; border-radius:6px; background:#fff; box-sizing: border-box;  display:none;}
.pop-wrap .edit-comment-pop {position: absolute; left:50%; top:50%; transform: translate(-50%, -50%); padding:40px 0; width:560px; border-radius:6px; background:#fff; box-sizing: border-box;  display:none;}
.pop-wrap .del-pop {position: absolute; left:50%; top:50%; transform: translate(-50%, -50%); padding:40px 0; width:520px; border-radius:6px; background:#fff; box-sizing: border-box;  display:none;}
.pop-wrap .del-pop .tit_box {padding:0 40px;}
.pop-wrap .del-pop .tit_box h3 {font-size: 24px; font-weight: bold; line-height: 1.33; letter-spacing: -0.24px; color: #000000; text-align:center;}
.pop-wrap .del-pop .text_del {font-size: 20px; line-height: 1.4; letter-spacing: -0.2px; text-align: center; color: #363636; margin-top:16px;}
.pop-wrap .del-reg-pop {position: absolute; left:50%; top:50%; transform: translate(-50%, -50%); padding:40px 0; width:520px; border-radius:6px; background:#fff; box-sizing: border-box;  display:none;}
